.container {
  background-size: cover;
  background-position: top center;
  border: 5px solid var(--bg-secondary);
  position: relative;
  overflow: hidden; }

.image {
  width: 100%; }

.modal {
  position: absolute;
  background: rgba(0, 0, 0, 0.7);
  padding: 20px;
  top: 0;
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  opacity: 0;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s; }
  .modal:hover {
    opacity: 1; }

.text {
  margin: auto;
  text-transform: uppercase;
  color: #ffffff;
  text-align: center; }

.name {
  margin-bottom: 20px; }

.link {
  font-size: 2em;
  padding: 10px;
  margin: 0 4px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s; }
  .link:hover {
    color: var(--color-active); }



